
.price {
  font-size: 0;
  line-height: 12px;
}
.price span {
  font-size: 10px;
}
.price .now {
  color: #f01414;
  margin-right: 8px;
}
.price .now .num {
  font-weight: 700;
  font-size: 14px;
}
.price .old {
  color: #93999f;
  text-decoration: line-through;
}



