
html,
body {
  line-height: 1;
  font-weight: 200;
  font-family: Helvetica, 'Hiragino Sans GB', 'Microsoft Yahei', '微软雅黑', Arial, sans-serif;
  height: 100%;
}
/*2倍和3倍图片*/
.clearfix {
  display: inline-block;
}
.clearfix::after {
  display: block;
  content: '.';
  height: 0;
  line-height: 0;
  visibility: hidden;
  clear: both;
}
.header {
  color: #fff;
  position: relative;
  background: rgba(7, 17, 27, 0.5);
  overflow: hidden;
}
.header .content-wrapper {
  position: relative;
  padding: 24px 12px 18px 24px;
  font-size: 0px;
}
.header .content-wrapper .avatar,
.header .content-wrapper .content {
  display: inline-block;
}
.header .content-wrapper .avatar {
  vertical-align: top;
}
.header .content-wrapper .avatar img {
  border-radius: 2px;
}
.header .content-wrapper .content {
  font-size: 14px;
  margin-left: 16px;
}
.header .content-wrapper .content .title {
  margin: 2px 0 8px 0;
}
.header .content-wrapper .content .title .brand {
  display: inline-block;
  width: 30px;
  height: 18px;
  vertical-align: top;
  background-image: url('../../common/img/brand@2x.png');
  background-size: 30px 18px;
  background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .title .brand {
    background-image: url('../../common/img/brand@3x.png');
}
}
.header .content-wrapper .content .title .name {
  font-size: 16px;
  color: #fff;
  font-weight: bold;
  line-height: 18px;
}
.header .content-wrapper .content .description {
  margin-top: 8px;
  font-size: 12px;
}
.header .content-wrapper .content .support {
  margin-top: 10px;
  font-size: 0;
  /*this.classMap = ['decrease','discount','special','invoice','guarantee'];*/
}
.header .content-wrapper .content .support .icon {
  display: inline-block;
  width: 12px;
  height: 12px;
  margin-right: 4px;
  background-size: 12px 12px;
  background-repeat: no-repeat;
  vertical-align: top;
}
.header .content-wrapper .content .support .text {
  line-height: 12px;
  font-size: 10px;
}
.header .content-wrapper .content .support .decrease {
  background-image: url('../../common/img/decrease_1@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .support .decrease {
    background-image: url('../../common/img/decrease_1@3x.png');
}
}
.header .content-wrapper .content .support .discount {
  background-image: url('../../common/img/discount_1@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .support .discount {
    background-image: url('../../common/img/discount_1@3x.png');
}
}
.header .content-wrapper .content .support .special {
  background-image: url('../../common/img/special_1@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .support .special {
    background-image: url('../../common/img/special_1@3x.png');
}
}
.header .content-wrapper .content .support .invoice {
  background-image: url('../../common/img/invoice_1@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .support .invoice {
    background-image: url('../../common/img/invoice_1@3x.png');
}
}
.header .content-wrapper .content .support .guarantee {
  background-image: url('../../common/img/guarantee_1@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .support .guarantee {
    background-image: url('../../common/img/guarantee_1@3x.png');
}
}
.header .content-wrapper .support-count {
  position: absolute;
  right: 12px;
  bottom: 14px;
  font-size: 0px;
  line-height: 12px;
  padding: 0px 8px;
  height: 24px;
  line-height: 24px;
  background: rgba(0, 0, 0, 0.2);
  color: #fff;
  border-radius: 14px;
}
.header .content-wrapper .support-count .count {
  font-size: 10px;
}
.header .content-wrapper .support-count .icon-keyboard_arrow_right {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin-left: 2px;
  text-align: center;
  line-height: 24px;
  vertical-align: top;
  font-size: 10px;
}
.header .notice-wrapper {
  position: relative;
  height: 28px;
  line-height: 28px;
  padding: 0 22px 0 12px;
  background-color: rgba(7, 17, 27, 0.2);
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.header .notice-wrapper .notice_title {
  display: inline-block;
  width: 22px;
  height: 12px;
  background-image: url('../../common/img/notice@2x.png');
  background-size: 22px 12px;
  background-repeat: no-repeat;
  margin-top: 8px;
  vertical-align: top;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .notice-wrapper .notice_title {
    background-image: url('../../common/img/notice@3x.png');
}
}
.header .notice-wrapper .notice_text {
  font-size: 10px;
  margin-left: 4px;
  vertical-align: top;
}
.header .notice-wrapper .icon-keyboard_arrow_right {
  position: absolute;
  font-size: 10px;
  right: 12px;
  top: 8px;
}
.header .background {
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  -webkit-filter: blur(10px);
          filter: blur(10px);
}
.header .background img {
  -webkit-transform: scale(0.65, 0.9);
          transform: scale(0.65, 0.9);
}
.header .detail {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background: rgba(7, 17, 27, 0.8);
  transition: all 0.5s;
  opacity: 1;
  /*backdrop-filter: blur(10px); ios 模糊背景*/
}
.header .detail .detail_wrapper {
  min-height: 100%;
  width: 100%;
}
.header .detail .detail_wrapper .detail_main {
  margin-top: 64px;
  padding-bottom: 64px;
}
.header .detail .detail_wrapper .detail_main .name {
  line-height: 16px;
  font-size: 16px;
  font-weight: 700;
  text-align: center;
}
.header .detail .detail_wrapper .detail_main .star_wrapper {
  margin-top: 16px;
  padding: 2px 0;
  text-align: center;
}
.header .detail .detail_wrapper .detail_main .title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 80%;
  margin: 28px auto 24px auto;
}
.header .detail .detail_wrapper .detail_main .title .line {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  position: relative;
  top: -6px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}
.header .detail .detail_wrapper .detail_main .title .text {
  padding: 0 12px;
  font-weight: 700;
  font-size: 14px;
}
.header .detail .detail_wrapper .detail_main .supports {
  width: 80%;
  margin: 0 auto;
}
.header .detail .detail_wrapper .detail_main .supports .support_item {
  padding: 0 12px;
  margin-bottom: 12px;
  font-size: 0;
}
.header .detail .detail_wrapper .detail_main .supports .support_item .icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  vertical-align: top;
  margin-right: 6px;
  background-size: 16px;
  background-repeat: no-repeat;
}
.header .detail .detail_wrapper .detail_main .supports .support_item .text {
  font-size: 12px;
  line-height: 16px;
}
.header .detail .detail_wrapper .detail_main .supports .support_item .decrease {
  background-image: url('../../common/img/decrease_2@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .detail .detail_wrapper .detail_main .supports .support_item .decrease {
    background-image: url('../../common/img/decrease_2@3x.png');
}
}
.header .detail .detail_wrapper .detail_main .supports .support_item .discount {
  background-image: url('../../common/img/discount_2@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .detail .detail_wrapper .detail_main .supports .support_item .discount {
    background-image: url('../../common/img/discount_2@3x.png');
}
}
.header .detail .detail_wrapper .detail_main .supports .support_item .special {
  background-image: url('../../common/img/special_2@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .detail .detail_wrapper .detail_main .supports .support_item .special {
    background-image: url('../../common/img/special_2@3x.png');
}
}
.header .detail .detail_wrapper .detail_main .supports .support_item .invoice {
  background-image: url('../../common/img/invoice_2@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .detail .detail_wrapper .detail_main .supports .support_item .invoice {
    background-image: url('../../common/img/invoice_2@3x.png');
}
}
.header .detail .detail_wrapper .detail_main .supports .support_item .guarantee {
  background-image: url('../../common/img/guarantee_2@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .detail .detail_wrapper .detail_main .supports .support_item .guarantee {
    background-image: url('../../common/img/guarantee_2@3x.png');
}
}
.header .detail .detail_wrapper .detail_main .supports .support_item:last-child {
  margin-bottom: 0;
}
.header .detail .detail_wrapper .detail_main .notice {
  width: 80%;
  margin: 0 auto;
}
.header .detail .detail_wrapper .detail_main .notice .content {
  padding: 0 12px;
  font-size: 12px;
  line-height: 24px;
}
.header .detail .detail_close {
  position: relative;
  width: 32px;
  height: 32px;
  margin: -64px auto 0 auto;
  clear: both;
  font-size: 32px;
  color: rgba(255, 255, 255, 0.5);
}
.header .detail.fade-enter,
.header .detail.fade-leave-active {
  opacity: 0;
}



