
html,
body {
  line-height: 1;
  font-weight: 200;
  font-family: Helvetica, 'Hiragino Sans GB', 'Microsoft Yahei', '微软雅黑', Arial, sans-serif;
  height: 100%;
}
/*2倍和3倍图片*/
.clearfix {
  display: inline-block;
}
.clearfix::after {
  display: block;
  content: '.';
  height: 0;
  line-height: 0;
  visibility: hidden;
  clear: both;
}
.tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  width: 100%;
  height: 40px;
  line-height: 40px;
  /* 2倍屏 */
  /* 3倍屏 */
}
.tab .tab-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
.tab .tab-item a {
  display: block;
  cursor: pointer;
  color: #4d555d;
}
.tab .tab-item .active {
  color: #f01414;
}
.tab::after {
  display: block;
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: rgba(7, 17, 27, 0.1);
  -webkit-transform-origin: left bottom;
  transform-origin: left bottom;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.tab::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
@media only screen and (-webkit-min-device-pixel-ratio: 3) {
.tab::after {
    -webkit-transform: scaleY(0.33);
    transform: scaleY(0.33);
}
}



