
html,
body {
  line-height: 1;
  font-weight: 200;
  font-family: Helvetica, 'Hiragino Sans GB', 'Microsoft Yahei', '微软雅黑', Arial, sans-serif;
  height: 100%;
}
/*2倍和3倍图片*/
.clearfix {
  display: inline-block;
}
.clearfix::after {
  display: block;
  content: '.';
  height: 0;
  line-height: 0;
  visibility: hidden;
  clear: both;
}
.ratingselect .rating_type {
  position: relative;
  padding: 18px 0;
  margin: 0 18px;
  /* 2倍屏 */
  /* 3倍屏 */
  font-size: 0;
}
.ratingselect .rating_type::after {
  display: block;
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: rgba(7, 17, 27, 0.1);
  -webkit-transform-origin: left bottom;
  transform-origin: left bottom;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.ratingselect .rating_type::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
@media only screen and (-webkit-min-device-pixel-ratio: 3) {
.ratingselect .rating_type::after {
    -webkit-transform: scaleY(0.33);
    transform: scaleY(0.33);
}
}
.ratingselect .rating_type .block {
  display: inline-block;
  padding: 8px 12px;
  margin-right: 8px;
  border-radius: 1px;
  color: #4d555d;
  font-size: 12px;
  line-height: 16px;
}
.ratingselect .rating_type .block .count {
  font-size: 8px;
  margin-left: 2px;
}
.ratingselect .rating_type .block.active {
  color: #fff;
}
.ratingselect .rating_type .block.positive {
  background: rgba(0, 160, 220, 0.2);
}
.ratingselect .rating_type .block.positive.active {
  background: #00a0dc;
}
.ratingselect .rating_type .block.negative {
  background: rgba(77, 85, 93, 0.2);
}
.ratingselect .rating_type .block.negative.active {
  background: #4d555d;
}
.ratingselect .switch {
  padding: 12px 18px;
  line-height: 24px;
  border-bottom: 1px solid rgba(7, 17, 27, 0.2);
  color: #93999f;
  font-size: 0;
}
.ratingselect .switch.on .icon-check_circle {
  color: #00c850;
}
.ratingselect .switch .icon-check_circle {
  display: inline-block;
  margin-right: 4px;
  font-size: 24px;
  vertical-align: top;
}
.ratingselect .switch .text {
  display: inline-block;
  font-size: 12px;
  vertical-align: top;
}



