
.carcontrol {
  font-size: 0;
}
.carcontrol div {
  display: inline-block;
}
.carcontrol .car_decrease,
.carcontrol .car_increase {
  padding: 6px;
  line-height: 24px;
  font-size: 24px;
  color: #00a0dc;
}
.carcontrol .car_decrease {
  transition: all 0.3s;
  opacity: 1;
  -webkit-transform: translate3D(0, 0, 0);
          transform: translate3D(0, 0, 0);
}
.carcontrol .car_decrease.slide-fade-enter-active,
.carcontrol .car_decrease.slide-fade-leave-active {
  transition: all 0.3s;
}
.carcontrol .car_decrease.slide-fade-enter,
.carcontrol .car_decrease.slide-fade-leave-active {
  -webkit-transform: translateX(36px) rotate(-180deg);
          transform: translateX(36px) rotate(-180deg);
  opacity: 0;
}
.carcontrol .car_count {
  vertical-align: top;
  width: 12px;
  padding-top: 6px;
  line-height: 24px;
  text-align: center;
  font-size: 10px;
  color: #93999f;
  transition: all 0.3s;
}



