
html,
body {
  line-height: 1;
  font-weight: 200;
  font-family: Helvetica, 'Hiragino Sans GB', 'Microsoft Yahei', '微软雅黑', Arial, sans-serif;
  height: 100%;
}
/*2倍和3倍图片*/
.clearfix {
  display: inline-block;
}
.clearfix::after {
  display: block;
  content: '.';
  height: 0;
  line-height: 0;
  visibility: hidden;
  clear: both;
}
.rating {
  position: absolute;
  width: 100%;
  top: 174px;
  bottom: 0;
  left: 0;
  overflow: hidden;
}
.rating .overview {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 18px 0;
}
.rating .overview .overview-left {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 137px;
          flex: 0 0 137px;
  padding: 6px 0;
  width: 137px;
  border-right: 1px solid rgba(7, 17, 27, 0.1);
  text-align: center;
}
@media only screen and (max-width: 320px) {
.rating .overview .overview-left {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 120px;
            flex: 0 0 120px;
    width: 120px;
}
}
.rating .overview .overview-left .score {
  margin-bottom: 6px;
  line-height: 28px;
  font-size: 24px;
  color: #ff9900;
}
.rating .overview .overview-left .title {
  margin-bottom: 8px;
  line-height: 12px;
  font-size: 12px;
  color: #07111b;
}
.rating .overview .overview-left .rank {
  line-height: 10px;
  font-size: 10px;
  color: #93999f;
}
.rating .overview .overview-right {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding: 6px 0 6px 24px;
}
@media only screen and (max-width: 320px) {
.rating .overview .overview-right {
    padding: 6px 0 6px 6px;
}
}
.rating .overview .overview-right .score-wrapper {
  margin-bottom: 8px;
  font-size: 0;
}
.rating .overview .overview-right .score-wrapper .title {
  display: inline-block;
  line-height: 18px;
  vertical-align: top;
  font-size: 12px;
  color: #07111b;
  margin-right: 12px;
}
.rating .overview .overview-right .score-wrapper .star {
  display: inline-block;
  vertical-align: top;
  margin-right: 12px;
}
.rating .overview .overview-right .score-wrapper .score {
  display: inline-block;
  line-height: 18px;
  vertical-align: top;
  font-size: 12px;
  color: #ff9900;
}
.rating .overview .overview-right .delivery-wrapper {
  font-size: 0;
}
.rating .overview .overview-right .delivery-wrapper .title {
  line-height: 18px;
  font-size: 12px;
  color: #07111b;
  margin-right: 12px;
}
.rating .overview .overview-right .delivery-wrapper .delivery {
  font-size: 12px;
  color: #93999f;
}
.rating .rating-wrapper {
  padding: 0 18px;
}
.rating .rating-wrapper .rating-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 18px 0;
  position: relative;
  /* 2倍屏 */
  /* 3倍屏 */
}
.rating .rating-wrapper .rating-item::after {
  display: block;
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: rgba(7, 17, 27, 0.1);
  -webkit-transform-origin: left bottom;
  transform-origin: left bottom;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.rating .rating-wrapper .rating-item::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
@media only screen and (-webkit-min-device-pixel-ratio: 3) {
.rating .rating-wrapper .rating-item::after {
    -webkit-transform: scaleY(0.33);
    transform: scaleY(0.33);
}
}
.rating .rating-wrapper .rating-item .avatar {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 28px;
          flex: 0 0 28px;
  width: 28px;
  margin-right: 12px;
}
.rating .rating-wrapper .rating-item .avatar img {
  border-radius: 50%;
}
.rating .rating-wrapper .rating-item .content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  position: relative;
}
.rating .rating-wrapper .rating-item .content .name {
  margin-bottom: 4px;
  line-height: 12px;
  font-size: 10px;
  color: #07111b;
}
.rating .rating-wrapper .rating-item .content .star-wrapper {
  margin-bottom: 6px;
  font-size: 0;
}
.rating .rating-wrapper .rating-item .content .star-wrapper .star {
  display: inline-block;
  vertical-align: top;
  margin-right: 6px;
}
.rating .rating-wrapper .rating-item .content .star-wrapper .delivery {
  display: inline-block;
  vertical-align: top;
  line-height: 12px;
  font-size: 10px;
  color: #93999f;
}
.rating .rating-wrapper .rating-item .content .text {
  line-height: 18px;
  color: #07111b;
  font-size: 12px;
}
.rating .rating-wrapper .rating-item .content .text.mb {
  margin-bottom: 8px;
}
.rating .rating-wrapper .rating-item .content .recommend {
  line-height: 16px;
  font-size: 0;
}
.rating .rating-wrapper .rating-item .content .recommend .icon-thumb_up,
.rating .rating-wrapper .rating-item .content .recommend .item {
  display: inline-block;
  margin: 0 8px 4px 0;
  font-size: 9px;
}
.rating .rating-wrapper .rating-item .content .recommend .icon-thumb_up {
  color: #00a0dc;
}
.rating .rating-wrapper .rating-item .content .recommend .item {
  padding: 0 6px;
  border: 1px solid rgba(7, 17, 27, 0.1);
  border-radius: 1px;
  color: #93999f;
  background: #fff;
}
.rating .rating-wrapper .rating-item .content .time {
  position: absolute;
  top: 0;
  right: 0;
  line-height: 12px;
  color: #93999f;
}



