
html,
body {
  line-height: 1;
  font-weight: 200;
  font-family: Helvetica, 'Hiragino Sans GB', 'Microsoft Yahei', '微软雅黑', Arial, sans-serif;
  height: 100%;
}
/*2倍和3倍图片*/
.clearfix {
  display: inline-block;
}
.clearfix::after {
  display: block;
  content: '.';
  height: 0;
  line-height: 0;
  visibility: hidden;
  clear: both;
}
.goods {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  width: 100%;
  top: 174px;
  bottom: 46px;
  overflow: hidden;
}
.goods .menu_wrapper {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 80px;
          flex: 0 0 80px;
  width: 80px;
  background: #f3f5f7;
}
.goods .menu_wrapper .menu_item {
  display: table;
  height: 54px;
  width: 56px;
  line-height: 14px;
  padding: 0 12px;
}
.goods .menu_wrapper .menu_item .text {
  position: relative;
  display: table-cell;
  width: 56px;
  vertical-align: middle;
  font-size: 12px;
  /* 2倍屏 */
  /* 3倍屏 */
}
.goods .menu_wrapper .menu_item .text::after {
  display: block;
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: rgba(7, 17, 27, 0.1);
  -webkit-transform-origin: left bottom;
  transform-origin: left bottom;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.goods .menu_wrapper .menu_item .text::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
@media only screen and (-webkit-min-device-pixel-ratio: 3) {
.goods .menu_wrapper .menu_item .text::after {
    -webkit-transform: scaleY(0.33);
    transform: scaleY(0.33);
}
}
.goods .menu_wrapper .menu_item .icon {
  display: inline-block;
  width: 12px;
  height: 12px;
  margin-right: 2px;
  background-size: 12px 12px;
  background-repeat: no-repeat;
  vertical-align: top;
}
.goods .menu_wrapper .menu_item .decrease {
  background-image: url('../../common/img/decrease_3@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.goods .menu_wrapper .menu_item .decrease {
    background-image: url('../../common/img/decrease_3@3x.png');
}
}
.goods .menu_wrapper .menu_item .discount {
  background-image: url('../../common/img/discount_3@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.goods .menu_wrapper .menu_item .discount {
    background-image: url('../../common/img/discount_3@3x.png');
}
}
.goods .menu_wrapper .menu_item .special {
  background-image: url('../../common/img/special_3@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.goods .menu_wrapper .menu_item .special {
    background-image: url('../../common/img/special_3@3x.png');
}
}
.goods .menu_wrapper .menu_item .invoice {
  background-image: url('../../common/img/invoice_3@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.goods .menu_wrapper .menu_item .invoice {
    background-image: url('../../common/img/invoice_3@3x.png');
}
}
.goods .menu_wrapper .menu_item .guarantee {
  background-image: url('../../common/img/guarantee_3@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.goods .menu_wrapper .menu_item .guarantee {
    background-image: url('../../common/img/guarantee_3@3x.png');
}
}
.goods .menu_wrapper .current {
  font-weight: 700;
  margin-top: -1px;
  background: #fff;
  z-index: 30;
}
.goods .menu_wrapper .current::after {
  display: none;
}
.goods .foods_wrapper {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.goods .foods_wrapper .title {
  padding-left: 14px;
  height: 26px;
  line-height: 26px;
  border-left: 2px solid #d9dde1;
  background: #f3f5f7;
  font-size: 12px;
  color: #93999f;
}
.goods .foods_wrapper .food_item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  margin: 18px;
  padding-bottom: 18px;
  /* 2倍屏 */
  /* 3倍屏 */
}
.goods .foods_wrapper .food_item::after {
  display: block;
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: rgba(7, 17, 27, 0.1);
  -webkit-transform-origin: left bottom;
  transform-origin: left bottom;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.goods .foods_wrapper .food_item::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
@media only screen and (-webkit-min-device-pixel-ratio: 3) {
.goods .foods_wrapper .food_item::after {
    -webkit-transform: scaleY(0.33);
    transform: scaleY(0.33);
}
}
.goods .foods_wrapper .food_item:last-child {
  margin-bottom: 0;
}
.goods .foods_wrapper .food_item:last-child::after {
  display: none;
}
.goods .foods_wrapper .food_item .icon {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 57px;
          flex: 0 0 57px;
  margin-right: 10px;
}
.goods .foods_wrapper .food_item .content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.goods .foods_wrapper .food_item .content .name {
  height: 14px;
  line-height: 14px;
  margin: 2px 0 8px 0;
  font-size: 14px;
  color: #07111b;
}
.goods .foods_wrapper .food_item .content .desc,
.goods .foods_wrapper .food_item .content .extra {
  line-height: 10px;
  font-size: 10px;
  color: #93999f;
  margin-bottom: 8px;
}
.goods .foods_wrapper .food_item .content .extra .count {
  margin-right: 12px;
}
.goods .foods_wrapper .food_item .content .carcontrol_wrapper {
  position: absolute;
  right: 0;
  bottom: 12px;
}



