
html,
body {
  line-height: 1;
  font-weight: 200;
  font-family: Helvetica, 'Hiragino Sans GB', 'Microsoft Yahei', '微软雅黑', Arial, sans-serif;
  height: 100%;
}
/*2倍和3倍图片*/
.clearfix {
  display: inline-block;
}
.clearfix::after {
  display: block;
  content: '.';
  height: 0;
  line-height: 0;
  visibility: hidden;
  clear: both;
}
.food {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 48px;
  z-index: 30;
  width: 100%;
  background: #fff;
  transition: all 0.35s;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.food.fade-enter,
.food.fade-leave-active {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.food .image_header {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
}
.food .image_header img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.food .image_header .icon-arrow_lift {
  position: absolute;
  top: 10px;
  left: 0;
  display: block;
  padding: 10px;
  font-size: 20px;
  color: #fff;
}
.food .content {
  padding: 18px;
  position: relative;
}
.food .content .title {
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
  line-height: 14px;
}
.food .content .detail {
  margin-bottom: 18px;
  font-size: 0;
  line-height: 10px;
  height: 10px;
}
.food .content .detail span {
  display: inline-block;
  font-size: 10px;
  color: #93999f;
}
.food .content .detail .sellCount {
  margin-right: 12px;
}
.food .content .carcontrol_wrapper {
  position: absolute;
  right: 12px;
  bottom: 12px;
}
.food .content .buy {
  position: absolute;
  right: 18px;
  bottom: 18px;
  z-index: 10;
  height: 24px;
  line-height: 24px;
  padding: 0 12px;
  box-sizing: border-box;
  border-radius: 12px;
  font-size: 10px;
  color: #fff;
  background: #00a0dc;
  transition: all 0.3s;
}
.food .content .buy.fade-enter,
.food .content .buy.fade-leave-active {
  opacity: 0;
}
.food .info {
  padding: 18px;
}
.food .info .title {
  line-height: 14px;
  margin-bottom: 6px;
  font-size: 14px;
  font-weight: 400;
  color: #07111b;
}
.food .info .text {
  padding: 0 8px;
  line-height: 24px;
  font-size: 12px;
  font-weight: 200;
  color: #4d555d;
}
.food .ratings {
  padding-top: 18px;
}
.food .ratings .title {
  line-height: 14px;
  margin-left: 18px;
  font-size: 14px;
  font-weight: 400;
  color: #07111b;
}
.food .ratings .rating_wrapper {
  padding: 0 18px;
}
.food .ratings .rating_wrapper .rating_item {
  position: relative;
  padding: 16px 0;
  /* 2倍屏 */
  /* 3倍屏 */
}
.food .ratings .rating_wrapper .rating_item::after {
  display: block;
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: rgba(7, 17, 27, 0.1);
  -webkit-transform-origin: left bottom;
  transform-origin: left bottom;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.food .ratings .rating_wrapper .rating_item::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
@media only screen and (-webkit-min-device-pixel-ratio: 3) {
.food .ratings .rating_wrapper .rating_item::after {
    -webkit-transform: scaleY(0.33);
    transform: scaleY(0.33);
}
}
.food .ratings .rating_wrapper .rating_item .user {
  position: absolute;
  right: 0;
  top: 16px;
  line-height: 12px;
  font-size: 0;
}
.food .ratings .rating_wrapper .rating_item .user .name {
  display: inline-block;
  vertical-align: top;
  margin-right: 6px;
  font-size: 10px;
  color: #93999f;
}
.food .ratings .rating_wrapper .rating_item .user .avatar {
  border-radius: 50%;
}
.food .ratings .rating_wrapper .rating_item .time {
  margin-bottom: 6px;
  line-height: 12px;
  font-size: 10px;
  color: #93999f;
}
.food .ratings .rating_wrapper .rating_item .text {
  line-height: 16px;
  font-size: 12px;
  color: #07111b;
}
.food .ratings .rating_wrapper .rating_item .text .icon {
  line-height: 16px;
  margin-right: 4px;
  font-size: 12px;
}
.food .ratings .rating_wrapper .rating_item .text .icon.icon-thumb_up {
  color: #00a0dc;
}
.food .ratings .rating_wrapper .rating_item .text .icon.icon-thumb_down {
  color: #93999f;
}
.food .ratings .rating_wrapper .no_rating {
  padding: 16px 0;
  font-size: 12px;
  color: #93999f;
}



