
html,
body {
  line-height: 1;
  font-weight: 200;
  font-family: Helvetica, 'Hiragino Sans GB', 'Microsoft Yahei', '微软雅黑', Arial, sans-serif;
  height: 100%;
}
/*2倍和3倍图片*/
.clearfix {
  display: inline-block;
}
.clearfix::after {
  display: block;
  content: '.';
  height: 0;
  line-height: 0;
  visibility: hidden;
  clear: both;
}
.seller {
  position: absolute;
  width: 100%;
  top: 174px;
  bottom: 0;
  left: 0;
  overflow: hidden;
}
.seller .overview {
  padding: 18px;
  position: relative;
}
.seller .overview .title {
  margin-bottom: 8px;
  line-height: 14px;
  color: #07111b;
  font-size: 14px;
}
.seller .overview .desc {
  padding-bottom: 18px;
  font-size: 0;
  position: relative;
  /* 2倍屏 */
  /* 3倍屏 */
}
.seller .overview .desc::after {
  display: block;
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: rgba(7, 17, 27, 0.1);
  -webkit-transform-origin: left bottom;
  transform-origin: left bottom;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.seller .overview .desc::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
@media only screen and (-webkit-min-device-pixel-ratio: 3) {
.seller .overview .desc::after {
    -webkit-transform: scaleY(0.33);
    transform: scaleY(0.33);
}
}
.seller .overview .desc .star {
  display: inline-block;
  margin-right: 8px;
  vertical-align: top;
}
.seller .overview .desc .text {
  display: inline-block;
  margin-right: 12px;
  line-height: 18px;
  font-size: 10px;
  vertical-align: top;
  color: #4d555d;
}
.seller .overview .remark {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-top: 18px;
}
.seller .overview .remark .block {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  border-right: 1px solid rgba(7, 17, 27, 0.1);
}
.seller .overview .remark .block:last-child {
  border: none;
}
.seller .overview .remark .block h2 {
  margin-bottom: 4px;
  line-height: 10px;
  font-size: 10px;
  color: #93999f;
}
.seller .overview .remark .block .content {
  line-height: 24px;
  font-size: 10px;
  color: #07111b;
}
.seller .overview .remark .block .content .count {
  font-size: 24px;
}
.seller .overview .favorite {
  position: absolute;
  width: 40px;
  right: 18px;
  top: 18px;
  text-align: center;
}
.seller .overview .favorite .icon-favorite {
  display: block;
  margin-bottom: 4px;
  line-height: 24px;
  font-size: 24px;
  color: #d4d6d9;
}
.seller .overview .favorite .icon-favorite.active {
  color: #f01414;
}
.seller .overview .favorite .text {
  line-height: 10px;
  font-size: 10px;
  color: #4d555d;
}
.seller .bulletin {
  padding: 18px 18px 0 18px;
}
.seller .bulletin .title {
  margin-bottom: 8px;
  line-height: 14px;
  color: #07111b;
  font-size: 14px;
}
.seller .bulletin .content-wrapper {
  padding: 0 12px 16px 12px;
}
.seller .bulletin .content-wrapper .content {
  line-height: 24px;
  font-size: 12px;
  color: #f01414;
}
.seller .bulletin .supports {
  font-size: 0;
}
.seller .bulletin .supports li {
  position: relative;
  padding: 16px 12px;
  /* 2倍屏 */
  /* 3倍屏 */
  /*this.classMap = ['decrease','discount','special','invoice','guarantee'];*/
}
.seller .bulletin .supports li::after {
  display: block;
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 1px;
  background-color: rgba(7, 17, 27, 0.1);
  -webkit-transform-origin: left top;
  transform-origin: left top;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.seller .bulletin .supports li::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
@media only screen and (-webkit-min-device-pixel-ratio: 3) {
.seller .bulletin .supports li::after {
    -webkit-transform: scaleY(0.33);
    transform: scaleY(0.33);
}
}
.seller .bulletin .supports li .icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  margin-right: 6px;
  background-size: 16px 16px;
  background-repeat: no-repeat;
  vertical-align: top;
}
.seller .bulletin .supports li .text {
  line-height: 16px;
  font-size: 12px;
  color: #07111b;
  vertical-align: top;
}
.seller .bulletin .supports li .decrease {
  background-image: url('../../common/img/decrease_4@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .bulletin .supports li .decrease {
    background-image: url('../../common/img/decrease_4@3x.png');
}
}
.seller .bulletin .supports li .discount {
  background-image: url('../../common/img/discount_4@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .bulletin .supports li .discount {
    background-image: url('../../common/img/discount_4@3x.png');
}
}
.seller .bulletin .supports li .special {
  background-image: url('../../common/img/special_4@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .bulletin .supports li .special {
    background-image: url('../../common/img/special_4@3x.png');
}
}
.seller .bulletin .supports li .invoice {
  background-image: url('../../common/img/invoice_4@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .bulletin .supports li .invoice {
    background-image: url('../../common/img/invoice_4@3x.png');
}
}
.seller .bulletin .supports li .guarantee {
  background-image: url('../../common/img/guarantee_4@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .bulletin .supports li .guarantee {
    background-image: url('../../common/img/guarantee_4@3x.png');
}
}
.seller .pics {
  padding: 18px;
}
.seller .pics .title {
  margin-bottom: 12px;
  line-height: 14px;
  color: #07111b;
  font-size: 14px;
}
.seller .pics .pic-wrapper {
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
}
.seller .pics .pic-wrapper .pic-list {
  font-size: 0;
}
.seller .pics .pic-wrapper .pic-list .pic-item {
  display: inline-block;
  margin-right: 6px;
  width: 120px;
  height: 90px;
}
.seller .pics .pic-wrapper .pic-list .pic-item:last-child {
  margin: 0;
}
.seller .info {
  padding: 18px 18px 0 18px;
  color: #07111b;
  font-size: 0;
}
.seller .info .title {
  padding-bottom: 12px;
  line-height: 14px;
  color: #07111b;
  font-size: 14px;
}
.seller .info .info-item {
  position: relative;
  padding: 16px 12px;
  line-height: 16px;
  font-size: 12px;
  /* 2倍屏 */
  /* 3倍屏 */
}
.seller .info .info-item::after {
  display: block;
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 1px;
  background-color: rgba(7, 17, 27, 0.1);
  -webkit-transform-origin: left top;
  transform-origin: left top;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.seller .info .info-item::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
@media only screen and (-webkit-min-device-pixel-ratio: 3) {
.seller .info .info-item::after {
    -webkit-transform: scaleY(0.33);
    transform: scaleY(0.33);
}
}



