
html,
body {
  line-height: 1;
  font-weight: 200;
  font-family: Helvetica, 'Hiragino Sans GB', 'Microsoft Yahei', '微软雅黑', Arial, sans-serif;
  height: 100%;
}
/*2倍和3倍图片*/
.clearfix {
  display: inline-block;
}
.clearfix::after {
  display: block;
  content: '.';
  height: 0;
  line-height: 0;
  visibility: hidden;
  clear: both;
}
.shopcar {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 48px;
  z-index: 90;
}
.shopcar .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #141d27;
}
.shopcar .content .content_left {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  font-size: 0;
}
.shopcar .content .content_left .logo_wrapper,
.shopcar .content .content_left .price,
.shopcar .content .content_left .desc {
  display: inline-block;
}
.shopcar .content .content_left .logo_wrapper {
  position: relative;
  top: -10px;
  margin: 0 12px;
  padding: 6px;
  width: 56px;
  height: 56px;
  box-sizing: border-box;
  vertical-align: top;
  border-radius: 50%;
  background: #141d27;
}
.shopcar .content .content_left .logo_wrapper .logo {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: #2b343c;
  text-align: center;
}
.shopcar .content .content_left .logo_wrapper .logo .icon-shopping_cart {
  font-size: 24px;
  line-height: 44px;
  color: #80858a;
}
.shopcar .content .content_left .logo_wrapper .logo .icon-shopping_cart.highlight {
  color: #fff;
}
.shopcar .content .content_left .logo_wrapper .logo.highlight {
  background: #00a0dc;
}
.shopcar .content .content_left .logo_wrapper .num {
  position: absolute;
  top: 0;
  right: 0;
  box-sizing: border-box;
  width: 24px;
  height: 16px;
  line-height: 16px;
  font-size: 9px;
  font-weight: 700;
  text-align: center;
  border-radius: 16px;
  color: #fff;
  background: #f01414;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
}
.shopcar .content .content_left .price {
  vertical-align: top;
  margin-top: 12px;
  padding-right: 12px;
  line-height: 24px;
  box-sizing: border-box;
  border-right: 1px solid rgba(255, 255, 255, 0.1);
  font-size: 16px;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.4);
}
.shopcar .content .content_left .price.highlight {
  color: #fff;
}
.shopcar .content .content_left .desc {
  vertical-align: top;
  margin-top: 12px;
  margin-left: 12px;
  line-height: 24px;
  box-sizing: border-box;
  font-size: 10px;
  color: rgba(255, 255, 255, 0.4);
}
.shopcar .content .content_right {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 105px;
          flex: 0 0 105px;
  width: 105px;
}
.shopcar .content .content_right .pay {
  font-size: 12px;
  font-weight: 700;
  line-height: 48px;
  text-align: center;
  color: rgba(255, 255, 255, 0.4);
}
.shopcar .content .content_right .pay.not-enough {
  background: #2b343c;
}
.shopcar .content .content_right .pay.enough {
  background: #00b43c;
  color: #fff;
}
.shopcar .ball_container .ball {
  position: fixed;
  left: 32px;
  bottom: 22px;
  z-index: 200;
  transition: all 0.4s cubic-bezier(0.49, -0.35, 0.75, 0.41);
}
.shopcar .ball_container .ball .inner {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #00a0dc;
  transition: all 0.4s linear;
}
.shopcar .shopcar_list {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  transition: all .4s ease;
  -webkit-transform: translate3d(0, -100%, 0);
          transform: translate3d(0, -100%, 0);
}
.shopcar .shopcar_list.translate-leave-active,
.shopcar .shopcar_list.translate-enter {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.shopcar .shopcar_list .list_header {
  height: 40px;
  line-height: 40px;
  padding: 0 18px;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
  background: #f3f5f7;
}
.shopcar .shopcar_list .list_header .title {
  float: left;
  font-size: 14px;
  color: #07111b;
}
.shopcar .shopcar_list .list_header .empty {
  float: right;
  font-size: 12px;
  color: #00a0dc;
}
.shopcar .shopcar_list .list_content {
  padding: 0 18px;
  max-height: 217px;
  overflow: hidden;
  background: #fff;
}
.shopcar .shopcar_list .list_content .food {
  position: relative;
  padding: 12px 0;
  box-sizing: border-box;
  /* 2倍屏 */
  /* 3倍屏 */
}
.shopcar .shopcar_list .list_content .food::after {
  display: block;
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: rgba(7, 17, 27, 0.1);
  -webkit-transform-origin: left bottom;
  transform-origin: left bottom;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.shopcar .shopcar_list .list_content .food::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
@media only screen and (-webkit-min-device-pixel-ratio: 3) {
.shopcar .shopcar_list .list_content .food::after {
    -webkit-transform: scaleY(0.33);
    transform: scaleY(0.33);
}
}
.shopcar .shopcar_list .list_content .food .name {
  line-height: 24px;
  font-size: 14px;
  color: #07111b;
}
.shopcar .shopcar_list .list_content .food .price {
  position: absolute;
  right: 90px;
  bottom: 12px;
  line-height: 24px;
  font-size: 14px;
  font-weight: 700;
  color: #f01414;
}
.shopcar .shopcar_list .list_content .food .carcontrol_wrapper {
  position: absolute;
  right: 0;
  bottom: 6px;
}
.list_mark {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -2;
  background: rgba(7, 17, 27, 0.8);
  opacity: 1;
}
.list_mark.fade-enter-active,
.list_mark.fade-leave-active {
  transition: all 0.4s;
}
.list_mark.fade-enter,
.list_mark.fade-leave-active {
  opacity: 0;
}



