
html,
body {
  line-height: 1;
  font-weight: 200;
  font-family: Helvetica, 'Hiragino Sans GB', 'Microsoft Yahei', '微软雅黑', Arial, sans-serif;
  height: 100%;
}
/*2倍和3倍图片*/
.clearfix {
  display: inline-block;
}
.clearfix::after {
  display: block;
  content: '.';
  height: 0;
  line-height: 0;
  visibility: hidden;
  clear: both;
}
.star {
  font-size: 0;
}
.star .star-item {
  display: inline-block;
  background-repeat: none;
}
.star .star-item:last-child {
  margin-right: 0!important;
}
.star-48 .star-item {
  width: 20px;
  height: 20px;
  margin-right: 22px;
  background-size: 20px 20px;
}
.star-48 .on {
  background-image: url('../../common/img/star48_on@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.star-48 .on {
    background-image: url('../../common/img/star48_on@3x.png');
}
}
.star-48 .half {
  background-image: url('../../common/img/star48_half@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.star-48 .half {
    background-image: url('../../common/img/star48_half@3x.png');
}
}
.star-48 .off {
  background-image: url('../../common/img/star48_off@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.star-48 .off {
    background-image: url('../../common/img/star48_off@3x.png');
}
}
.star-36 .star-item {
  width: 15px;
  height: 15px;
  margin-right: 6px;
  background-size: 15px 15px;
}
.star-36 .on {
  background-image: url('../../common/img/star36_on@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.star-36 .on {
    background-image: url('../../common/img/star36_on@3x.png');
}
}
.star-36 .half {
  background-image: url('../../common/img/star36_half@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.star-36 .half {
    background-image: url('../../common/img/star36_half@3x.png');
}
}
.star-36 .off {
  background-image: url('../../common/img/star36_off@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.star-36 .off {
    background-image: url('../../common/img/star36_off@3x.png');
}
}
.star-24 .star-item {
  width: 10px;
  height: 10px;
  margin-right: 3px;
  background-size: 10px 10px;
}
.star-24 .on {
  background-image: url('../../common/img/star24_on@2x.png');
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
